body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  width: 500px;
  left: 475px;
  position: absolute;
}

.top {
  width: 500px;
  height: 300px;
  margin-top: 20px;
  background-image: url(/trainer-details/static/media/pexels-ketut-subiyanto-4720236.d697704d.jpg);
  background-size: 100% 100%;
  left: 475px;
  box-shadow: 1px 1px 1px 0px black;
  animation: slider 12s infinite linear;
  position: fixed;
}

@keyframes slider {
  0% {
    background-image: url(/trainer-details/static/media/pexels-ketut-subiyanto-4720236.d697704d.jpg);
  }
  35% {
    background-image: url(/trainer-details/static/media/pexels-mister-mister-3490348.d69af5c5.jpg);
  }
  75% {
    background-image: url(/trainer-details/static/media/pexels-victor-freitas-841130.1306dab7.jpg);
  }
}
.mid {
  background-color: white;
  margin-top: 250px;
  width: 500px;
  height: 500px;
  border-radius: 40px;
  position: absolute;
}
.intro {
  padding-left: 30px;
  color: rgb(55, 133, 163);
}

.h {
  width: 85%;
  margin-left: 35px;
}

.footer {
  width: 500px;
  height: 50px;
  background-color: white;
  position: fixed;
  bottom: 0px;
  padding-bottom: 0px;
  box-shadow: 0px 0px 0px 0px black;
}
.buttonone {
  width: 250px;
  height: 50px;
  background-color: white;
  border: none;
  color: black;
  font-weight: bold;
  text-align: center;
  display: inline-block;
  font-size: 19px;
  box-shadow: 0px 0px 8px 0px gray;
}
.buttontwo {
  width: 250px;
  height: 50px;
  background-color: #625298;
  border: none;
  color: white;
  font-weight: bold;
  text-align: center;
  display: inline-block;
  font-size: 19px;
  box-shadow: 0px 0px 8px 0px gray;
}

